import { FunctionComponent, useCallback, useState } from 'react';
import styles from './login-page.module.css';
import { EmailInput, PasswordInput, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, Redirect, useLocation } from 'react-router-dom';
import { ILocationState } from '../utils/types';
import { login } from '../services/actions/user-actions';
import { useAppDispatch, useAppSelector } from '../services/store';

const LoginPage: FunctionComponent = () => {
    const appDispatch = useAppDispatch();
    const auth = useAppSelector(store => store.auth);
    const [form, setValue] = useState({ email: '', password: '' });
    const location = useLocation<ILocationState>();

    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setValue({ ...form, [e.target.name]: e.target.value });
    };

    const onFormSubmit = useCallback(
        e => {
            e.preventDefault();
            appDispatch(login(form));
        },
        [auth, form]
    );

    if (auth.user) {
        return (
            <Redirect to={location?.state?.from || { pathname: '/' }} />
        );
    }

    return (!auth.loading ?
        <div className={styles.root_container}>
            <h1 className="text text_type_main-medium">
                
            </h1>
            <form onSubmit={onFormSubmit} className={styles.form}>
                <div className="mt-6">
                    <EmailInput onChange={onChange} value={form.email} name={'email'} />
                </div>
                <div className="mt-6">
                    <PasswordInput onChange={onChange} value={form.password} name={'password'} />
                </div>
                <div className="mt-6">
                    <Button type="primary" size="medium">
                        
                    </Button>
                </div>
            </form>
            <div className="mt-20 text text_color_inactive text_type_main-small">
                  ?<Link className={"ml-4 " + styles.link} to='/register'></Link>
            </div>
            <div className="mt-4 text text_color_inactive text_type_main-small">
                 ?<Link className={"ml-4 " + styles.link} to='/forgot-password'> </Link>
            </div>
        </div>
        : null
    )

}

export default LoginPage